<template>
  <b-row>
    <!-- children must be cols -->

    <template v-if="bots">
      <BotCard v-for="(bot, i) in bots" :key="i" :bot="bot" :variant="i" />
    </template>
    <GridCol v-else>
      <div class="h-100 d-flex align-items-center justify-content-center">
        <b-spinner label="Loading bots..." variant="info" />
      </div>
    </GridCol>

    <NewBotCard />
  </b-row>
</template>

<script>
import {mapActions, mapState} from 'vuex'
import {BIconPencil, BIconTrash} from 'bootstrap-vue'
import BotCard from '@/components/BotCard.vue'
import GridCol from '@/components/GridCol.vue'
import NewBotCard from '@/components/NewBotCard.vue'

export default {
  components: {
    BotCard,
    GridCol,
    NewBotCard,
  },

  data() {
    return {
      agents: null,
    }
  },

  computed: {
    ...mapState({
      bots: state => state.bots.bots,
    }),
  },

  mounted() {
    this.fetchBots()
  },

  methods: {
    ...mapActions(['fetchBots']),
  },
}
</script>
