<template>
  <b-navbar type="dark" variant="dark">
    <b-navbar-brand to="/">Botworx</b-navbar-brand>

    <b-navbar-nav v-if="isAuthenticated" class="ml-auto">
      <b-nav-item-dropdown right no-caret>
        <template v-slot:button-content>
          <b-avatar v-if="user.image" :src="user.image" size="sm" />
          <span v-else>{{ user.name }}</span>
        </template>
        <b-dd-text tag="em">
          <small class="text-muted">{{ user.email }}</small>
        </b-dd-text>
        <b-dd-divider />
        <b-dd-item href="#" disabled>Profile</b-dd-item>
        <b-dd-item href="#" disabled>Billing</b-dd-item>
        <b-dd-divider />
        <b-dd-item href="#" disabled>Docs</b-dd-item>
        <b-dd-item href="#" disabled>Support</b-dd-item>
        <b-dd-divider />
        <b-dd-item @click="onLogout">Logout</b-dd-item>
      </b-nav-item-dropdown>
    </b-navbar-nav>
  </b-navbar>
</template>

<script>
import {mapGetters, mapState} from 'vuex'

export default {
  computed: {
    ...mapGetters(['isAuthenticated']),

    ...mapState({
      user: state => state.auth.user,
    }),
  },

  methods: {
    onLogout() {
      this.$store.dispatch('logout', {vm: this}).then(() => {
        this.$router.push({name: 'r-login'})
      })
    },
  },
}
</script>
