<template>
  <GridCard>
    <span
      v-if="bot.isEnabled"
      v-b-tooltip.bottomright
      class="status rounded-circle bg-success"
      title="Enabled"
    />
    <span
      v-else
      v-b-tooltip.bottomright
      class="status rounded-circle bg-danger"
      title="Disabled"
    />

    <b-avatar :variant="variants[variant % variants.length]" />

    <h4 class="my-4 font-weight-light">{{ bot.name }}</h4>

    <div
      class="actions d-flex w-100 justify-content-between align-items-center"
    >
      <span
        v-b-tooltip.bottomright
        class="h6 mb-0"
        title="Delete"
        role="button"
        @click="isDeleting = true"
      >
        <BIconTrash class="text-danger" />
      </span>
      <span
        v-b-tooltip.bottom
        class="h5 mb-0"
        title="Edit"
        role="button"
        @click="onEdit"
      >
        <BIconPencil class="text-secondary" />
      </span>
      <span v-b-tooltip.bottomleft.html class="h3 mb-0" :title="infoText">
        <BIconInfo class="text-primary" />
      </span>
    </div>

    <b-modal
      v-model="isDeleting"
      centered
      hide-header
      ok-title="Delete"
      ok-variant="danger"
      :busy="isDeletePending"
      @ok="onDelete"
    >
      <div class="text-center">
        <BIconExclamationTriangle class="my-4 text-warning" font-scale="5" />
        <h3 class="text-muted">
          Are you sure you want to delete <em>{{ bot.name }}</em
          >?
        </h3>
      </div>
    </b-modal>
  </GridCard>
</template>

<script>
import {mapActions} from 'vuex'
import dayjs from 'dayjs'
import escapeHTML from 'escape-html'
import {
  BIconExclamationTriangle,
  BIconInfo,
  BIconPencil,
  BIconTrash,
} from 'bootstrap-vue'
import GridCard from '@/components/GridCard.vue'

export default {
  components: {
    BIconExclamationTriangle,
    BIconInfo,
    BIconPencil,
    BIconTrash,
    GridCard,
  },

  props: {
    bot: {
      type: Object,
      required: true,
    },
    variant: {
      type: Number,
      required: true,
    },
  },

  data() {
    return {
      isDeleting: false,
      isDeletePending: false,

      variants: [
        'primary',
        'dark',
        'success',
        'danger',
        'warning',
        'info',
        'secondary',
        'light',
      ],
    }
  },

  computed: {
    infoText() {
      const agentName = this.bot.agent
        ? escapeHTML(this.bot.agent.displayName)
        : '<em>Unassigned</em>'
      const createdAt = dayjs(this.bot.createdAt).format('ll')
      return `
        Agent: ${agentName}<br>
        Created: ${createdAt}
      `
    },
  },

  methods: {
    onEdit() {
      this.$router.push({name: 'r-editor', params: {botId: this.bot.id}})
    },

    async onDelete(e) {
      e.preventDefault() // do not hide modal yet
      this.isDeletePending = true
      try {
        await this.deleteBot(this.bot.id)
      } catch (e) {
        console.error(e)
      }
      this.isDeletePending = false
    },

    ...mapActions(['deleteBot']),
  },
}
</script>

<style scoped>
.status {
  position: absolute;
  top: 1.25rem;
  left: 1.25rem;
  width: 12px;
  height: 12px;
}

.actions {
  margin-bottom: -0.8rem;
}
</style>
