<template>
  <b-row>
    <b-col cols="12" md="6" lg="4">
      <b-card>
        <template v-slot:header>
          <BIconWrench /> Configure
        </template>

    <b-form @submit.prevent="onSubmit">
      <b-form-group>
      <b-form-checkbox v-model="isEnabled" switch>
        Enabled
      </b-form-checkbox>
      </b-form-group>

      <b-form-group label="Bot name:">
        <b-form-input v-model="botName" maxlength="20" required />
      </b-form-group>

      <b-button type="submit" variant="primary">Save</b-button>
    </b-form>
  </b-card>
    </b-col>
  </b-row>
</template>

<script>
import {mapActions, mapState} from 'vuex'
import {BIconWrench} from 'bootstrap-vue'

export default {
  components: {
    BIconWrench
  },

  props: {
    botId: {
      type: String,
      required: true
    }
  },

  data() {
    return {
      botName: undefined,
      isEnabled: undefined,
    }
  },

  computed: {
    ...mapState({
      currentBot: state => state.bots.currentBot
    }),
  },

  async created() {
    try {
      const bot = await this.getOrFetchBot(this.botId)
      this.$store.commit('setCurrentBot', bot)
      // update fields since the data is now available
      this.botName = bot.name
      this.isEnabled = bot.isEnabled
    } catch(e) {
      // no bot with id botId, or could not fetch
      this.$router.push({name: 'r-home'})
    }
  },

  methods: {
    ...mapActions(['getOrFetchBot', 'updateCurrentBot']),

    onSubmit() {
      this.updateCurrentBot({
        name: this.botName,
        isEnabled: this.isEnabled
      })
    },
  }
}
</script>
