export default {
  state: {
    user: null,
  },

  getters: {
    isAuthenticated(state) {
      return !!state.user
    },
  },

  mutations: {
    setCurrentUser(state, userInfo) {
      state.user = userInfo
    },
  },

  actions: {
    login({commit}, {vm}) {
      return vm.$gapi.signIn().then(user => {
        commit('setCurrentUser', user)
      })
    },

    logout({commit}, {vm}) {
      return vm.$gapi.signOut().then(() => {
        commit('setCurrentUser', null)
      })
    },
  },
}
