<template>
  <b-row align-h="center" align-v="center" class="h-100">
    <b-col sm="auto">
      <b-card no-body class="m-4 shadow-sm">
        <b-card-body class="text-center">
          <b-card-title title-tag="h3" class="m-5">Login</b-card-title>
          <GoogleLoginBtn
            class="my-5 mx-sm-5 mx-auto"
            @click.native="onLogin"
          />
        </b-card-body>
      </b-card>
    </b-col>
  </b-row>
</template>

<script>
import GoogleLoginBtn from '@/components/GoogleLoginBtn.vue'

export default {
  components: {
    GoogleLoginBtn,
  },
  methods: {
    onLogin() {
      this.$store
        .dispatch('login', {vm: this})
        .then(this.onLoginSuccess)
        .catch(this.onLoginFailure)
    },
    onLoginSuccess() {
      this.$router.push({name: 'r-home'})
    },
    onLoginFailure(err) {
      console.error('Not signed in: %s', err.error)
    },
  },
}
</script>
