import Vue from 'vue'
import dayjs from 'dayjs'
import localizedFormat from 'dayjs/plugin/localizedFormat'
import '@/plugins/bootstrap-vue'
import GAPI from '@/plugins/gapi'

function init() {
  // DayJS
  dayjs.extend(localizedFormat)

  // Google API
  const gapiConfig = {
    clientId:
      '776881837099-mro8g67vfh7fjl5v4m3fvoi9a673anla.apps.googleusercontent.com',
    scope: [
      'profile',
      'email',
      'openid',
      'https://www.googleapis.com/auth/dialogflow',
      'https://www.googleapis.com/auth/cloud-platform',
    ].join(' '),
    discoveryDocs: [
      'https://dialogflow.googleapis.com/$discovery/rest?version=v2',
    ],
  }
  Object.defineProperty(Vue.prototype, '$gapi', {
    value: new GAPI(gapiConfig),
  })
}

export default init
