export default {
  state: {
    bots: null,
    currentBot: null,
  },

  mutations: {
    setBots(state, bots) {
      state.bots = bots
    },
    setCurrentBot(state, bot) {
      state.currentBot = bot
    },
    updateBot(state, newBot) {
      const bot = state.bots.find(bot => bot.id === newBot.id)
      Object.assign(bot, newBot)
    },
    removeBot(state, botId) {
      state.bots = state.bots.filter(bot => bot.id !== botId)
    },
  },

  actions: {
    fetchBots({commit, state}) {
      const bots = [
        {
          id: '9457836',
          createdAt: '2020-05-16T15:07:25.507Z',
          name: 'ZDownBot',
          agent: null,
          isEnabled: true,
        },
        {
          id: '78904123',
          createdAt: '2020-05-16T23:07:25.507Z',
          name: 'AUpBot',
          agent: null,
          isEnabled: false,
        },
      ]
      if (state.bots) {   // FIXME: testing only
        return Promise.resolve(state.bots)
      }
      // TODO: Request bots
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('setBots', bots)
          resolve(bots)
        }, 200)
      })
    },

    getOrFetchBot({dispatch, state}, botId) {
      return new Promise((resolve, reject) => {
        const bot = state.bots.find(bot => bot.id === botId)
        bot ? resolve(bot) : reject()
      }).catch(() => {
        // TODO: Fetch bot
        return dispatch('fetchBots')
          .then(bots => bots.find(bot => bot.id === botId))
      })
    },

    updateCurrentBot({commit, state}, data) {
      const bot = state.bots.find(bot => bot.id === state.currentBot.id)
      commit('updateBot', {...bot, ...data})
      // TODO: Update bot
      return Promise.resolve(bot)
    },

    deleteBot({commit}, botId) {
      // TODO: Delete bot
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('removeBot', botId)
          resolve()
        }, 1500)
      })
    },
  },
}
