import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '@/store/'
import Login from '@/views/Login.vue'
import Home from '@/views/Home.vue'
import Editor from '@/views/Editor.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'r-home',
    component: Home,
    meta: {
      title: 'Home',
      requireAuth: false,
    },
  },
  {
    path: '/login',
    name: 'r-login',
    component: Login,
    meta: {
      title: 'Login',
      requireAuth: false,
    },
  },
  {
    path: '/:botId',
    name: 'r-editor',
    component: Editor,
    props: true,  // convert route params to component prop
    meta: {
      title: 'Editor',
      requireAuth: false,
    },
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

// Navigation guard
router.beforeEach((to, from, next) => {
  const requireAuth = to.meta.requireAuth

  if (requireAuth === undefined || requireAuth) {
    if (store.getters.isAuthenticated) next()
    else next({name: 'r-login'})
  } else {
    // FIXME
    if (store.getters.isAuthenticated && false) next({name: 'r-home'})
    else next()
  }
})

// Update page title
router.beforeEach((to, from, next) => {
  document.title = to.meta.title ? to.meta.title + ' - Botworx' : 'Botworx'
  next()
})

export default router
